import Cookie from "js-cookie";
import { Base64 } from "js-base64";
export default {
	computed: {
		base_img_url() {
			return process.env.apiPublicUrl;
		},
	},
	data() {
		return {
			doctors: [],
			search: null,
			pagination: {
				current: 1,
				total: 1,
			},
		};
	},
	mounted() {
		this.getDoctors();
	},
	methods: {
		isEmpty(obj) {
			if (typeof obj == "number") return false;
			else if (typeof obj == "string") return obj.length == 0;
			else if (Array.isArray(obj)) return obj.length == 0;
			else if (typeof obj == "object")
				return obj == null || Object.keys(obj).length == 0;
			else if (typeof obj == "boolean") return false;
			else return !obj;
		},
		getDoctors(param) {
			if (this.search !== null) {
				this.$store
					.dispatch("getDoctors", {
						doctorsURL:
							"doctors?page=" +
							this.pagination.current +
							"&search=" +
							decodeURIComponent(this.search),
					})
					.then((response) => {
						this.doctors = this.$store.getters.doctors.data;
						this.pagination.current = this.$store.getters.doctors.current_page;
						this.pagination.total = this.$store.getters.doctors.last_page;
					});
			} else {
				if (param) {
					this.$store
						.dispatch("getDoctors", { doctorsURL: param })
						.then((response) => {
							this.doctors = this.$store.getters.doctors.data;
							this.pagination.current = this.$store.getters.doctors.current_page;
							this.pagination.total = this.$store.getters.doctors.last_page;
						});
				} else {
					this.$store
						.dispatch("getDoctors", {
							doctorsURL: "doctors?page=" + this.pagination.current,
						})
						.then((response) => {
							this.doctors = this.$store.getters.doctors.data;
							this.pagination.current = this.$store.getters.doctors.current_page;
							this.pagination.total = this.$store.getters.doctors.last_page;
						});
				}
			}
		},
		onPageChange() {
			this.getDoctors();
		},
	},
};
